import { observable, makeObservable, configure, computed, action, autorun } from 'mobx'
import { v4 as uuid } from 'uuid'

import parse from 'url-parse'
import ReactPlayer from 'react-player'

configure({ enforceActions: 'never' })

export interface Cut {
	startTime: number
	endTime: number
}

export interface Player {
	id: number
	isVisible: boolean
	playing: boolean
	muted: boolean
	url: string
	instance?: ReactPlayer
	cutIndex?: number
}

export interface CutStore {
	id: string
	index: number
	url: string
	cut: Cut
}

export interface Source {
	id: string
	index: number
	url: string
}

export enum Mode {
	Create = 'create',
	PlayOne = 'playOne',
	PlayAll = 'playAll',
	Show = 'show',
}

const url = parse(window.location.href, true)
let urlStory: CutStore[] | null = null
let urlSources: Source[] | null = null

if (url.query.story) {
	const raw = decodeURIComponent(url.query.story)
	urlStory = raw.split(',').map((player) => {
		const parts = player.split('::')

		return {
			id: parts[0],
			index: parseFloat(parts[1]),
			url: parts[2],
			cut: {
				startTime: parseFloat(parts[3]),
				endTime: parseFloat(parts[4]),
			},
		}
	})
}

if (url.query.video) {
	const raw = decodeURIComponent(url.query.video)
	urlSources = raw.split(',').map((source) => {
		const parts = source.split('::')

		return {
			id: parts[0],
			index: parseFloat(parts[1]),
			url: parts[2],
		}
	})
}

const hasUrl = urlSources && urlSources.length > 0

class Store {
	@observable sources: Source[] = urlSources || []
	@observable mode: Mode = !hasUrl ? Mode.Create : Mode.Show
	@observable cuts: CutStore[] = urlStory || []
	@observable players: Player[] = [0, 1, 2].map((player, i) => {
		return {
			id: i,
			isVisible: i === 0,
			playing: false,
			muted: false,
			url: this.sources[0] ? this.sources[0].url : '',
		}
	})
	@observable activePlayerIndex: number = 0
	@observable currentCutIndex: number = this.cuts.length > 0 ? 0 : -1
	@observable currentSourceIndex: number = this.sources.length > 0 ? 0 : -1

	@computed get shareUrl() {
		if (this.sources.length === 0) return null
		const baseUrl = window.location.href.split('?')[0]

		return `${baseUrl}?video=${encodeURIComponent(
			this.sources
				.map((source) => {
					return [source.id, source.index, source.url].join('::')
				})
				.join(',')
		)}&story=${encodeURIComponent(
			this.cuts
				.map((player, i) => {
					return [player.id, player.index, player.url, player.cut?.startTime, player.cut?.endTime].join('::')
				})
				.join(',')
		)}`
	}

	@computed get trailerLength() {
		return this.cuts.reduce((duration, player, index) => {
			if (!player.cut) return duration
			return duration + player.cut.endTime - player.cut.startTime
		}, 0)
	}

	@computed get nextPlayerIndex() {
		if (this.activePlayerIndex === this.players.length - 1) return 0
		return this.activePlayerIndex + 1
	}

	@computed get nextCutIndex() {
		if (this.currentCutIndex === this.cuts.length - 1) return 0
		return this.currentCutIndex + 1
	}

	@action
	addCut(cut: Cut) {
		this.cuts.push({
			id: uuid(),
			index: this.cuts.length,
			url: this.sources[this.currentSourceIndex].url,
			cut: {
				startTime: cut.startTime,
				endTime: cut.endTime,
			},
		})
	}

	@action
	addSource(url: string) {
		this.sources = [
			...this.sources,
			{
				id: uuid(),
				index: this.sources.length,
				url: url,
			},
		]

		this.changeModeToCreate(this.sources.length - 1)
	}

	@action
	changeSource(sourceIndex: number) {
		this.currentSourceIndex = sourceIndex
		this.changeModeToCreate(sourceIndex)
	}

	@action
	removeCut(cutIndex: number) {
		if (
			this.players[this.activePlayerIndex].playing &&
			(cutIndex === this.currentCutIndex || cutIndex === this.nextCutIndex)
		)
			return

		if (cutIndex === this.currentCutIndex) {
			this.changeModeToCreate(0)
			return
		}

		this.cuts = this.cuts
			.filter((item) => item.index !== cutIndex)
			.map((item, index) => {
				item.index = index
				return item
			})
	}

	@action
	changeModeToCreate(sourceIndex: number) {
		this.mode = Mode.Create

		this.currentSourceIndex = sourceIndex

		for (let i = 0; i < this.players.length; i += 1) {
			if (i === this.activePlayerIndex) {
				this.players[i] = {
					...this.players[i],
					isVisible: true,
					url: this.sources[sourceIndex].url,
					playing: false,
				}

				this.currentCutIndex = -1
			} else {
				this.players[i].isVisible = false
				this.players[i].playing = false
			}

			// @ts-ignore
			this.players[i].instance?.getInternalPlayer().stopVideo()
		}
	}

	@action
	changeModeToPlayOne(cutIndex: number) {
		this.mode = Mode.PlayOne

		this.players[this.activePlayerIndex] = {
			...this.players[this.activePlayerIndex],
			url: this.cuts[cutIndex].url,
			cutIndex,
			playing: false,
		}
		this.currentCutIndex = cutIndex

		setTimeout(() => this.startPlayingCurrentCut(this.cuts[this.currentCutIndex].cut), 500)
	}

	@action
	changeModeToPlayAll() {
		if (this.cuts.length === 0) {
			this.changeModeToCreate(0)
			this.players[this.activePlayerIndex].playing = true
			return
		}

		this.mode = Mode.PlayAll

		this.setDefaultPlayerState()
	}

	@action
	changeModeToShow() {
		this.mode = Mode.Show
		this.setDefaultPlayerState()
	}

	@action
	startPlayingCurrentCut(cut: Cut) {
		if (!cut) return

		this.players[this.activePlayerIndex].instance?.seekTo(cut.startTime)

		setTimeout(() => {
			this.players[this.activePlayerIndex].instance?.seekTo(cut.startTime)
			this.players[this.activePlayerIndex].playing = true
		}, 2000)
	}

	setDefaultPlayerState() {
		this.players[this.activePlayerIndex].playing = false

		setTimeout(() => {
			for (let i = 0; i < this.players.length; i += 1) {
				// @ts-ignore
				this.players[i].instance?.getInternalPlayer().stopVideo()

				if (i === 0) {
					this.players[i] = {
						id: this.players[i].id,
						instance: this.players[i].instance,
						isVisible: true,
						muted: false,
						url: this.cuts[0].url,
						playing: false,
						cutIndex: 0,
					}
				} else {
					this.players[i] = {
						id: this.players[i].id,
						instance: this.players[i].instance,
						isVisible: false,
						muted: false,
						url: this.cuts[0].url,
						playing: false,
					}
				}
			}

			this.activePlayerIndex = 0
			this.currentCutIndex = 0
			this.startPlayingCurrentCut(this.cuts[this.currentCutIndex].cut)
		}, 1000)
	}

	constructor() {
		makeObservable(this)
		autorun(() => console.log(`--> In ${this.mode.toUpperCase()} mode`))
	}
}

const store = new Store()

export default store
