import React, { useEffect, useState } from 'react';
import { Grid as GiphyGrid } from '@giphy/react-components';
import { GiphyFetch } from '@giphy/js-fetch-api';
import ResizeObserver from 'react-resize-observer';

import styles from './GifsPanel.module.sass';
import { ContentView } from '../ContentView';
import { Input } from '../Input';

const giphyFetch = new GiphyFetch('IPKlChsULe1Qlt19wbrWHlnjDr04SZa8');

enum FetchType {
	Trending = 'trending',
	Search = 'search',
}

interface GifsProps {
	width: number;
	searchText: string;
	fetchType: FetchType;
}

function Gifs({ width, searchText, fetchType }: GifsProps) {
	function fetchGifs(offset: number) {
		if (fetchType === FetchType.Search) return giphyFetch.search(searchText, { offset, limit: 10 });
		if (fetchType === FetchType.Trending) return giphyFetch.trending({ offset, limit: 10 });
		return giphyFetch.trending({ offset, limit: 10 });
	}

	return <GiphyGrid fetchGifs={fetchGifs} width={width} columns={4} gutter={6} />;
}

function GifsPanel() {
	const giphyGrid = React.createRef<any>();

	const [width, setWidth] = useState<number | null>(null);
	const [searchText, setSearchText] = useState<string>('');
	const [showGrid, setShowGrid] = useState(true);

	useEffect(() => {
		if (!giphyGrid) return;

		setWidth(giphyGrid.current.offsetWidth);
	}, [giphyGrid]);

	useEffect(() => {
		const delayDebounce = setTimeout(() => {
			setShowGrid(true);
		}, 1000);

		return () => clearTimeout(delayDebounce);
	}, [searchText]);

	function onChangeSearchText(value: string) {
		setShowGrid(false);
		setSearchText(value);
	}

	return (
		<ContentView title='Add Gif'>
			<div ref={giphyGrid} className={styles.root}>
				<Input placeholder='Search with GIPHY' value={searchText} onChange={onChangeSearchText} />
				{width && (
					<div className={styles.gifsPanel}>
						{showGrid && searchText === '' && (
							<Gifs width={width} searchText={searchText} fetchType={FetchType.Trending} />
						)}
						{showGrid && searchText !== '' && (
							<Gifs width={width} searchText={searchText} fetchType={FetchType.Search} />
						)}
						<ResizeObserver
							onResize={({ width }) => {
								// setWidth(width)
							}}
						/>
					</div>
				)}
			</div>
		</ContentView>
	);
}

export default GifsPanel;
