import React, { useState } from 'react';
import { AudioPanel } from '../AudioPanel';
import GifsPanel from '../GifsPanel';
import { Sidebar } from '../Sidebar';
import SourcesPanel from '../SourcePanel';
import { TextPanel } from '../TextPanel';
import { tabs, FRAMES_TAB, GIFS_TAB, TEXT_TAB, AUDIO_TAB } from './constants';
import styles from './contentBlock.module.scss';

const COMPONENT_MAP: Record<string, React.ReactNode> = {
	[FRAMES_TAB]: <SourcesPanel />,
	[GIFS_TAB]: <GifsPanel />,
	[TEXT_TAB]: <TextPanel />,
	[AUDIO_TAB]: <AudioPanel />,
};

export const ContentBLock: React.FC = () => {
	const [activeTab, setActiveTab] = useState(FRAMES_TAB);

	const handleClick = (id: string) => {
		setActiveTab(id);
	};

	return (
		<div className={styles.container}>
			<Sidebar tabs={tabs} handleClick={handleClick} activeTab={activeTab} />
			<div className={styles.content}>{COMPONENT_MAP[activeTab]}</div>
		</div>
	);
};
