import React, { useState } from 'react';
import { observer } from 'mobx-react';
import { Grid } from '@material-ui/core';
import styles from './SourcesPanel.module.sass';
import store, { Mode } from '../../store';
import { ContentView } from '../ContentView';
import { Input } from '../Input';
import { COLOR_THEME } from '../Button/constants';
import { Button } from '../Button';
import CutsPanel from '../CutsPanel';

const SourcesPanel: React.FC = () => {
	const [url, setUrl] = useState('');

	const onChangeUrl = (value: string) => setUrl(value);

	const addSource = () => {
		store.addSource(url);
		setUrl('');
	};

	const changeSource = (sourceIndex: number) => {
		store.changeSource(sourceIndex);
	};

	return (
		<ContentView title='All frames'>
			{store.mode !== Mode.Show && (
				<Grid container direction='column'>
					<Grid className={styles.searchLine} item>
						<Input value={url} onChange={onChangeUrl} placeholder='Paste the video link here' />
					</Grid>
					<Grid item container spacing={2}>
						{store.sources.map((source: any) => (
							<Grid key={source.id} item>
								<Button
									onClick={() => changeSource(source.index)}
									colorTheme={
										source.index === store.currentSourceIndex
											? COLOR_THEME.RED_THEME
											: COLOR_THEME.BLUE_THEME
									}
								>
									Source {source.index + 1}
								</Button>
							</Grid>
						))}
						<Grid item>
							<Button onClick={addSource} withBorder disabled={url === ''}>
								Add source
							</Button>
						</Grid>
					</Grid>
				</Grid>
			)}
			<CutsPanel />
		</ContentView>
	);
};

export default observer(SourcesPanel);
