import React from 'react';
import styles from './button.module.scss';
import cx from 'classnames';
import { COLOR_THEME } from './constants';

type Props = {
	children?: any;
	disabled?: boolean;
	colorTheme?: string;
	withBorder?: boolean;
	onClick: () => void;
};

export const Button: React.FC<Props> = ({ children, disabled, colorTheme, withBorder, onClick }) => {
	return (
		<button
			className={cx(styles.button, {
				[styles.disabled]: disabled,
				[styles.withBorder]: withBorder,
				[styles.redTheme]: colorTheme === COLOR_THEME.RED_THEME,
				[styles.blueTheme]: colorTheme === COLOR_THEME.BLUE_THEME,
			})}
			disabled={disabled}
			onClick={onClick}
		>
			{children}
		</button>
	);
};
