import React from 'react';
import { TabItem } from '../TabItem';
import styles from './sidebar.module.scss';

type Props = {
	activeTab: string;
	tabs: { id: string; icon: string }[];
	handleClick: (value: string) => void;
};

export const Sidebar: React.FC<Props> = ({ tabs, activeTab, handleClick }) => {
	return (
		<div className={styles.container}>
			{tabs.map(({ icon, id }) => (
				<TabItem icon={icon} isActive={id === activeTab} key={id} value={id} onClick={handleClick} />
			))}
		</div>
	);
};
