import React from 'react';
import styles from './input.module.scss';
import copyIcon from './assets/img/copyIcon.svg';
import closeIcon from './assets/img/closeIcon.svg';

type Props = {
	value: string;
	placeholder?: string;
	onChange: (value: string) => void;
};

export const Input: React.FC<Props> = ({ value, placeholder, onChange }) => {
	const copyText = () => {
		navigator.clipboard.writeText(value).then(function () {
			alert('    ');
		});
	};
	return (
		<div className={styles.container}>
			<input
				onChange={(e) => onChange(e.target.value)}
				value={value}
				placeholder={placeholder}
				className={styles.input}
			/>
			{value && (
				<div className={styles.buttonsBlock}>
					<button className={styles.clearButton} onClick={() => onChange('')}>
						<img src={closeIcon} />
					</button>
					<button className={styles.copyButton} onClick={copyText}>
						<img src={copyIcon} />
					</button>
				</div>
			)}
		</div>
	);
};
